<template>
  <div class="lac-header">
    <Button
      v-if="!isShowAside"
      icon="pi pi-align-justify"
      @click="onShowAside"
      text
    />
    <RouterLink class="lac-header__link" :to="{ name: 'map' }"
      >Карта</RouterLink
    >
    <RouterLink class="lac-header__link" :to="{ name: 'offers' }"
      >Предложения</RouterLink
    >
    <RouterLink class="lac-header__link" :to="{ name: 'requests' }"
      >Спрос</RouterLink
    >
    <template v-if="me && me.role?.value === 'admin'">
      <RouterLink class="lac-header__link" :to="{ name: 'chain' }"
        >Сделки</RouterLink
      >
    </template>
    <RouterLink class="lac-header__link" :to="{ name: 'events' }"
      >События</RouterLink
    >
    <template v-if="me && me.role?.value === 'admin'">
      <RouterLink class="lac-header__link" :to="{ name: 'adminUsers' }">
        Пользователи
      </RouterLink>
      <RouterLink class="lac-header__link" :to="{ name: 'adminRoles' }">
        Должности
      </RouterLink>
    </template>

    <div style="margin-left: auto; display: flex">
      <RouterLink class="lac-header__link" :to="{ name: 'profile' }">
        Профиль
        <span class="pi pi-user"></span>
      </RouterLink>

      <RouterLink
        class="lac-header__link lac-header__link--logout"
        :to="{ name: 'logout' }"
      >
        <span
          class="pi pi-sign-out"
          style="color: var(--lac-color__red)"
        ></span>
      </RouterLink>
    </div>
  </div>
</template>
<script setup>
import { storeToRefs } from "pinia";
import { useStore } from "@/store";
import { useStoreUser } from "@/store/user";
const { isShowAside } = storeToRefs(useStore());

const storeUser = useStoreUser();
const { me } = storeToRefs(storeUser);

function onShowAside() {
  isShowAside.value = true;
}
</script>
<style lang="scss">
.lac-header {
  display: flex;
  flex-wrap: wrap;
  border-bottom: thin solid var(--lac-color__border);

  &__link {
    display: block;
    height: 40px !important;
    padding: 10px 16px;
    margin-bottom: -1px;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    border: thin solid transparent;
    border-bottom: none;
    color: var(--lac-color__link);
    text-decoration: none;
    transition-duration: var(--lac-transition__hover-off);
    transition-property: color, opacity;

    &--logout {
      display: flex;
      align-items: center;
      justify-content: center;

      &:hover {
        opacity: 0.5;
      }
    }

    &:hover {
      color: var(--lac-color__link-hover);
      transition-duration: var(--lac-transition__hover-on);
    }

    &.router-link-exact-active {
      border-color: var(--lac-color__border);
      background-color: var(--lac-color__bg);
      color: var(--lac-color__text);
      transition-duration: var(--lac-transition__hover-on);
    }
  }
}
</style>
<script>
export default {
  components: {},
};
</script>
