export const rotateDate = (str: string) => {
  if (!str.length) {
    return str;
  }

  let split = str.split("-");
  if (split.length > 1) {
    return split.reverse().join(".");
  }
  split = split[0].split(".");
  if (split.length > 1) {
    return split.reverse().join("-");
  }
  return str;
};
